<template>
  <div>
    <ul class="items">
      <li class="item"></li>
      <li class="item">
        <span :class="needYear?'need-year':'buxian'" @click="toggleYear"></span>
        <el-date-picker v-model="year" type="year" placeholder="" v-show="needYear"></el-date-picker>
      </li>
      <li class="item">
        <span :class="needMonth?'need-year':'buxian'" @click="toggleMonth"></span>
        <el-date-picker v-model="month" type="month" placeholder="" v-show="needMonth"></el-date-picker>
      </li>
      <li class="item">
        <span :class="needName?'need-year':'buxian'" @click="toggleName"></span>
        <el-input v-model="name" placeholder="" style="width: 200px;" v-show="needName"></el-input>
      </li>
      <li class="item">
        <span :class="needType?'need-year':'buxian'" @click="toggleType"></span>
        <el-input v-model="type" placeholder="" style="width: 200px;" v-show="needType"></el-input>
      </li>
    </ul>
    <el-button type="primary"></el-button>

    <p class="text-center caption-color"></p>
    <el-table :data="tableData4" border style="width: 100%"
      :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn4" :key="index" :prop="item.prop" :label="item.label"/>
      <el-table-column
        fixed="right"
        label=""
        width="80">
        <template slot-scope="scope">
          <el-button @click="getResDetail(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="60%"
      :before-close="handleClose">
      <el-table :data="tableData1" border style="width: 100%"
        :header-cell-style="hdStyle" :cell-style="cellStyle">
        <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
      </el-table>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <!-- <el-button type="primary" @click="dialogVisible = false"> </el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data() {
    return {
      month: new Date(),
      tableCloumn4: [
        { prop: 'col1', label: '' },
        { prop: 'col2', label: '' },
        { prop: 'col3', label: '' },
        { prop: 'col4', label: '' },
        { prop: 'col5', label: '' },
        { prop: 'col6', label: '' },
        { prop: 'col7', label: '' },
        { prop: 'col8', label: '' },
      ],
      tableData4: [
        { col1: 'P20190103', col2: '', col3: '', col4: '', col5: '', col6: '', col7: '', col8: ''},
        { col1: 'P20190104', col2: '-', col3: 'PAC', col4: '5%', col5: '18000', col6: '1000', col7: '5.56%', col8: '0'},
      ],
      tableCloumn1: [
        { prop: 'col1', label: '' },
        { prop: 'col2', label: '' },
        { prop: 'col3', label: '' },
        { prop: 'col4', label: '' },
      ],
      tableData1: [
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: 'PAC', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
        { col1: '', col2: '', col3: '', col4: ''},
      ],
      year: new Date(),
      month: new Date(),
      name: '',
      type: '',
      needYear: true,
      needMonth: true,
      needName: false,
      needType: false,
      dialogVisible: false
    }
  },
  methods: {
    toggleYear() {
      this.needYear = !this.needYear
    },
    toggleMonth() {
      this.needMonth = !this.needMonth
    },
    toggleName() {
      this.needName = !this.needName
    },
    toggleType() {
      this.needType = !this.needType
    },
    getResDetail(i) {
      console.log(i)
      this.dialogVisible = true
    },
    handleClose(done) {
      done()
      // this.$confirm('')
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    }
  }
}
</script>

<style lang="scss" scoped>
.items {
  border: 1px solid #eee;
  .item {
    &:first-child {
      background-color: #eee;
    }
    &:not(:first-child) {
      font-weight: 600;
    }
    height: 50px;
    padding: 0 20px;
    line-height: 50px;
    font-size: 15px;
    .buxian {
      padding: 0 20px;
      color: #a44;
      font-weight: normal;
      cursor: pointer;
    }
    .need-year {
      padding: 0 20px;
      color: #777;
      font-weight: normal;
      cursor: pointer;
    }
  }
}

</style>



